<style lang="less" scoped>
  #mainPage {
    position: relative;
    height: 100%;
    min-height: 45rem;
    background-size: 100% 100%;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
    background-color: #f6f7f8;
    .components {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      padding-bottom: 4rem;
    }
  }
  
  .tabbarBox {
    position: absolute;
    z-index: 2;
    bottom: 0;
    width: 100%;
    height: 4rem;
    background-color: #1A181A;
    .tabbar {
      color: #fff;
      display: inline-block;
      width: 24%;
      text-align: center;
      height: 3rem;
      padding: .5rem 0;
      box-sizing: border-box;
    }
    .tabbarChoosed {
      color: #FF005F;
    }
  }
</style>

<template>
	<div id="mainPage" >
		<transition name="fade">
      <div class="components" v-show='column ==0'>
		    <hotBox ></hotBox>
      </div>
    </transition>

    <transition name='fade' >
      <div class="components" v-show='column ==1'>
       <sudoku ></sudoku>
       </div>
    </transition>

    <transition name="fade">
      <div class="components" v-show='column ==2'>
        <sendWish ></sendWish>
      </div>
    </transition>

     <transition name="fade">
       <div class="components" v-show='column ==3'>
        <resume ></resume>
       </div>
    </transition>
    

		<div class="tabbarBox">
			<div class="tabbar" :class='{tabbarChoosed : column == index}' @click='chooseThis(index, item )' v-for='(item, index) in data'>
				<div class="iconfont" v-html="item.icoUrl"></div>
				<span>{{item.name}}</span>
			</div>
		</div>

	</div>
</template>

<script>
  import Vue from 'vue'
  import hotBox from '../components/hot.vue'
  import sudoku from '../components/sudoku.vue'
  import sendWish from '../components/sendWish.vue'
  import resume from '../components/resume.vue'
  import temp from '../../libs/temp/getPageMsg.json'; //栏目
  import {
    Indicator
  } from 'mint-ui';


  export default {
    components: {
      hotBox,
      sudoku,
      sendWish,
      resume
    },
    data() {
      return {
        column: this.$route.query.column || 0, //选择的哪个栏目
        title: ['头条推荐', '新运宫格', '天天五折', '简历'],
        showShareBtn: false, //app渠道才显示分享按钮
        shareBtn: false, //是否点了分享
        shareInfo: {}, //分享的信息，标题等
        data: temp.columnList, //模拟的主栏目数据

      }
    },

    methods: {
      chooseThis(index, item) {
        let msg = {
            '主栏目': item.name,
            '详情': item
          }
          // console.log(msg)
        this.column = index;

      },

    },

    mounted() {
      let para = this.$route.query;
      // console.log(para)


      if (this.data[this.column]) {
        this.chooseThis(this.column, this.data[this.column])
      }

    },
  }
</script>


