<style lang="less">
  .sendWish {
    position: relative;
    .inputBox {
      position: relative;
      width: 90%;
      left: 5%;
      input {
        display: inline-block;
        border: none;
        /*background-color: @inputBg;*/
        color: #ca5a5a;
        width: 78%;
        line-height: 3rem;
        height: 3rem;
        font-size: 1.5rem;
        padding-left: 10px;
        margin: 0;
      }
      .inputBtn {
        display: inline-block;
        background-color: #F37E00;
        height: 3rem;
        line-height: 3rem;
        width: 19%;
        margin: 0;
        text-align: center;
        color: #fff;
        font-size: 1.5rem;
      }
    }
    .wishBox {
      position: relative;
      width: 90%;
      left: 5%;
      .wishList {
        font-size: 1.2rem;
        display: inline-block;
        padding: 5px;
        margin: 10px 10px 0 0;
        color: #999;
        border: 1px #999 dashed;
      }
    }
    .btnChange {
      position: absolute;
      right: 1rem;
      width: 20%;
      margin-top: -2rem;
      line-height: 2rem;
      text-align: center;
      font-size: 1.2rem;
      color: #f37e00;
      border-radius: 5px;
      background-color: rgba(0, 0, 0, 0.2);
    }
  }
</style>
<template>
	<div class="sendWish flex-none" >
	<!--<div class="sendWish flex-none" v-on:click="showCoverMsg">-->
		<div class="inputBox">
			<input type="text" placeholder="不是真的留言o(￣ヘ￣o❀)" maxlength="15" id="" v-model='commentMsg' />
			<div class="inputBtn" @click='sendComment'>发布</div>
		</div>
		<div class="wishBox">
			<div class="wishList" v-for='item in commentList' @click='changeComment(item)'>{{item.comment}}</div>
		</div>
		<div class="btnChange" v-if='commentList.length == 5' @click='randomComment()'>换一批</div>
	</div>
</template>
<script>
  import axios from 'axios'
  import api from '../api'
  import queryString from 'querystring';
  import choiceCommentList from '../../libs/temp/sw_choiceCommentList.json';

  export default {
    props: ['addCommentList'],
    data() {
      return {
        commentMsg: '',
        commentList: [],
        sendNum: 0, //发送了几条祝福
      }
    },
    methods: {
      changeComment(item) { //选择随机的祝福语
        this.commentMsg = item.comment;
      },
      sendComment() { //发送祝福语
        let $this = this;
        this.sendNum += 1;
        console.log('现在发送了 ' + this.sendNum + '条祝福')
        if (this.commentMsg && this.sendNum < 4) {
          console.log('提交的祝福语： ' + this.commentMsg)
          let tempObj = {
            comment: this.commentMsg,
            operate: 1,
            nops: 0
          }
          this.addCommentList.push(tempObj)

          this.commentMsg = '';
        } else if (this.sendNum > 3) {
          this.showCoverMsg()
        }
        setTimeout(function() {
          $this.setSendNum()
        }, 1000)
      },
      showCoverMsg() {
        this.setSendNum()
        this.$emit('showCoverMsg')
      },


      setSendNum() {
        this.sendNum = 0
      },
      randomComment() { //取随机出现的祝福语

        let newCommentList = [];
        while (newCommentList.length < 5) {
          let i = Math.floor(Math.random() * choiceCommentList.data.length);
          if (newCommentList.indexOf(choiceCommentList.data[i]) < 0) {
            newCommentList.push(choiceCommentList.data[i])
          }
        }
        this.commentList = newCommentList;
      },



    },
    mounted() {
      this.randomComment()
    }
  }
</script>


