<template>
	<div>
		<div class="wish " v-for="(wish, index) in showedList" v-if='!wish.isDelete' :key="wish.comment" :style="{ top: wish.top }" @animationend="flyEnd(index)" @click="likeIt(index)">
			<span class="icon iconfont">{{wish.operate == 1 ? '&#xe678;':  '&#xe60e;'}}</span> {{wish.nops ? '+'+wish.nops : ''}} {{wish.comment}}
		</div>
	</div>
</template>
<script>
  // import clone from 'lodash/clone'
  import axios from 'axios'
  import api from '../api'
  import queryString from 'querystring';

  export default {
    props: ['commentList', 'userInfos', 'shareInfos'],
    data() {
      return {
        tempList: this.commentList.length ? clone(this.commentList) : [this.commentList],
        showedList: [],
      }
    },
    watch: {
      commentList: function() {
        this.tempList = clone(this.commentList);
        this.showedList = []
      }
    },
    methods: {
      randomTop() {
        return Math.random() * 90 + '%'
      },
      flyEnd(index) {
        //      console.log('flyEnd'+index)
        if (index == this.showedList.length - 1) {
          this.tempList = this.showedList;
          this.showedList = []
        }
      },
      likeIt(index) {
        if (this.showedList[index].operate != 0) {
          this.showedList[index].operate = 0;
          this.showedList[index].nops += 1;
        }
      },
    },
    created() {
      let danmu = this
      if (danmu.tempList.length) {
        let wish = danmu.tempList.shift();
        wish.top = danmu.randomTop();
        danmu.showedList.push(wish);
      }
      setInterval(function() {
        if (danmu.tempList.length) {
          let wish = danmu.tempList.shift();
          wish.top = danmu.randomTop();
          danmu.showedList.push(wish);
        }
      }, 2500)
    },

  }
</script>
<style lang="less" scoped="">
  .wish {
    font-size: 1.3rem;
    position: absolute;
    display: inline-block;
    white-space: nowrap;
    animation: flyFast linear;
    -moz-animation: flyFast linear;
    -webkit-animation: flyFast linear;
    -o-animation: flyFast linear;
    animation-duration: 10s;
    padding: .2rem .3rem;
    border-radius: .2rem;
    background-color: #fff;
    color: #d00;
    right: -100%;
    box-shadow: 3px 3px 3px #a40000;
    vertical-align: middle;
    .icon {
      vertical-align: middle;
    }
  }
  
  @keyframes flyFast {
    from {
      right: 0;
    }
    to {
      right: 150%;
    }
  }
  
  @-webkit-keyframes flyFast {
    from {
      right: -10%;
    }
    to {
      right: 100%;
    }
  }
  
  @-moz-keyframes flyFast {
    from {
      right: -10%;
    }
    to {
      right: 100%;
    }
  }
  
  @-o-keyframes flyFast {
    from {
      right: -10%;
    }
    to {
      right: 100%;
    }
  }
</style>


