</style>

<template>
	<div id="mainPage">
		<div class="mainView" id="mainView">

			<div class="topBox ">
				<div class="userImgBox">
					<img class="userImg" :src="pageView.headImgUrl? pageView.headImgUrl: ''" />
				</div>
				<!--<div class="topBtn" >我的奖品</div>-->
				<div class="topBtn" @click='showExplain()'>活动说明</div>
			</div>

			<div class="prizeBox">
				<img class="prizeTitle" src="../../libs/imgs/title_sukudo.png" />
				<div class="prizeContBox flex flex-align-center flex-pack-center">
					<div class="contBox flex flex-align-center flex-pack-center flex_" v-for="(prize, index) in prizes"  v-bind:class="{contBox_9 : prizes.length == 9}">
						<img class="btnBgcR" src="../../libs/imgs/btnBgcR.png" />
						<img class="contImg" v-if='!prize.prizeUrl' src="../../libs/imgs/btnP1.png" />
						<img class="contImg" v-if='prize.prizeUrl' :src='prize.prizeUrl? prize.prizeUrl :""' />

						<p class="contMsg" v-show='prize.prizeName !="么么哒"'>{{prize.prizeName}}</p>
						<div v-show='prizeObj.isCover' :class="( prizeObj.prizeTemp%(prizes.length) == index && prizeObj.prizeTemp != null)?'': 'cover'"></div>
					</div>
				</div>
			</div>
			<div class="choiceP">您还有{{pageView.currencyNum}}次抽奖机会</div>

			<div class="btnRow flex flex-atuo">
				<div class="btn btn1 flex-atuo" @click='getPrize()'>
					<p>{{activityStateMsg[activityState]}}</p>
				</div>
				<div class="btn btn2 flex-atuo" @click='showMoreChoice'>
					<p v-show='pageView.moreChoice'>更多<br/>机会</p>
				</div>
			</div>

			<div class="marquee " v-show='prizeObj.showWinList'>
				<span class="iconfont">&#xe647;</span>
				<div class="showWinList ">
					<span class="fading" v-for='item in prizeObj.showWinList'>{{item}}</span>
				</div>
			</div>

		</div>

		<!--遮盖层, 中奖结果，  coverState＝ 2-->
		<div class="stateView " v-if='coverState == "2"'>
			<div class="popupBox">
				<img v-if='prizeObj.getPrizes == 2 ' class="popupBoxTitle" src="../../libs/imgs/sorry.png" />
				<img v-if='prizeObj.getPrizes == 0 ' class="popupBoxTitle" src="../../libs/imgs/sorry_.png" />
				<img v-if='prizeObj.getPrizes == 0' class="faceImg " src="../../libs/imgs/noWin.png" />
				<img v-if='prizeObj.getPrizes == 2' class="faceImg" src="../../libs/imgs/noChance.png" />

				<img v-if='prizeObj.getPrizes == 1' class="popupBoxTitle" src="../../libs/imgs/win.png" />
				<img v-if='prizeObj.getPrizes == 1' class="popupBoxColourBar" src="../../libs/imgs/colourBar.png" />
				<img v-if='prizeObj.getPrizes == 1' class="faceImg prizeImg" :src="prizeObj.prizeUrl ? prizeObj.prizeUrl :''" />
				<div class="stateMsg">{{prizeObj.prizeName}}</div>
			</div>
			<div class="stateBtnRow">
				<div v-if='prizeObj.getPrizes != 2' class="stateBtn stateBtnOk" @click='getPrize'>再来一把</div>
				<div class="stateBtn stateBtnBack" @click='showMainPage'>返回</div>
			</div>

		</div>

		<!--遮盖层 活动说明  coverState＝3-->
		<div class="stateView " v-if='coverState == "3"'>
			<div class="explain">
				<div class="explainTitle">
					<span class="iconfont" @click='showMainPage'>&#xe62b;</span> 活动说明
				</div>
				<div class="explainMsg" v-html="pageView.explainsDesc"></div>
			</div>
		</div>

	</div>

</template>

<script>
  import api from '../api'
  import pageView from '../../libs/temp/sudoku_pageView.json';
  import prizeList from '../../libs/temp/sudoku_prizeList.json';
  import prizeObj from '../../libs/temp/sudoku_prizeObj.json';
  import {
    Indicator,
    MessageBox
  } from 'mint-ui';
  export default {
    data() {
      return {
        activityId: this.$route.query.activityId, //活动id
        coverState: '', //遮盖层: 1更多抽奖机会 2 中奖结果  3活动说明
        activityState: 1, //活动状态 0未开始，1进行中，2暂停，3结束
        activityStateMsg: ['活动未开始', '来一把幸运', '活动已暂停', '活动已结束', ],
        drawState: '', //抽奖状态
        prizeObj: prizeObj, // isCover:抽奖遮盖  getPrizes:中奖状态，0未中奖，1中奖，2没有抽奖机会  prizeTemp:中奖结果  prizeName:如果中奖了，奖品名字 ，没中奖为提示语  prizeUrl:如果中奖了，奖品图片URl   drawing: 是否抽奖进行时  showWinList:中奖名字
        pageView: pageView, //headImgUrl:用户头像 drawQualifications:抽奖资格 currencyNum:抽奖机会  explainsDesc:活动说明  moreChoice:更多抽奖机会
        prizeList: prizeList,
        prizes: '',
      }
    },
    computed: {

    },
    methods: {
      getPrizeList() { //奖品列表
        let prizesList = this.prizeList.prizes; //返回回来的奖品列表
        // console.log(prizeList)
        let addPrizes = {
          prizeName: '么么哒'
        }; //默认的奖品
        let prizesLength; //页面显示奖品数量
        //奖品数量如果少于9，页面宫格显示3*3 .
        //奖品数大于8小于16， 页面宫格显示 4*4
        if (prizesList.length < 9) {
          prizesLength = 9
        } else if (prizesList.length < 16) {
          prizesLength = 16
        } else { //大于16个就n*4
          let rowNum = Math.round(prizesList.length / 4);
          prizesLength = rowNum * 4;
        }
        //如果奖品不够塞满宫格，就要加'么么哒'
        let newList = new Array;
        if (prizesList.length < prizesLength) {
          for (let i = 0; i < (prizesLength - prizesList.length); i++) {
            newList[i] = addPrizes
          }
        }
        //两个数组接起来
        prizesList = prizesList.concat(newList);
        //重排
        function randomSort() {
          return Math.random() - 0.5
        }
        let x;
        x = prizesList.sort(randomSort)
        x = x.sort(randomSort)
        x = x.sort(randomSort)
        x = x.sort(randomSort)

        this.prizes = x.sort(randomSort)

      },


      //开始抽奖
      getPrize() {
        console.log('抽奖Ing')
        let $this = this;
        if (!this.prizeObj.drawing && this.pageView.currencyNum > 0) {
          this.prizeObj.drawing = true
          this.pageView.currencyNum = this.pageView.currencyNum - 1;
          this.clearMsg(); //清除中奖信息

          let prizesList = this.prizes; //奖品列表
          let index = prizesList.length;

          let i = Math.floor(Math.random() * prizesList.length);
          let randomNum = {
            'index': i,
            'name': prizesList[i].prizeName
          }
          console.log(randomNum)

          if (prizesList[i].prizeName != '么么哒') {
            //中奖
            this.prizeObj.getPrizes = 1;
            this.prizeObj.prizeName = prizesList[i].prizeName;
            this.prizeObj.prizeUrl = prizesList[i].prizeUrl;
          } else {
            //没中奖
            this.prizeObj.getPrizes = 0;
            this.prizeObj.prizeName = '么么哒，天天好心情';
          }
          this.prizeObj.isCover = true;
          //抽奖滚动的动画

          setTimeout(function() {
            $this.lotteryDraw(i, 2, $this.prizes.length);
          }, 200)
        } else if (!this.prizeObj.drawing && this.pageView.currencyNum < 1) {

          this.cover(2);
          this.prizeObj.getPrizes = 2
          this.prizeObj.prizeName = '抽奖机会已经用完！'

        }
      },


      //抽奖动画
      lotteryDraw: function(winNum, num, turns) { //（中奖的index， 转几圈， 几个奖品）
        let $this = this;
        let turnNum = num * turns;
        //先转两圈，第三圈停下来
        if (this.prizeObj.prizeTemp < winNum + turnNum) {
          this.prizeObj.prizeTemp = this.prizeObj.prizeTemp === null ? 0 : (this.prizeObj.prizeTemp + 1);
          let interval; //移动时间
          if (this.prizeObj.prizeTemp < ((winNum + turnNum) - 5)) {
            interval = 100
          } else {
            interval = 100 + (this.prizeObj.prizeTemp - ((winNum + turnNum) - 5)) * 80
          }
          setTimeout(
            function() {
              $this.lotteryDraw(winNum, num, turns)
            },
            interval)
        } else {
          console.log("stop");
          this.prizeObj.drawing = false;
          this.cover(2); // 出中奖遮盖
        }
      },

      //显示更多抽奖机会
      showMoreChoice() {
        if (this.pageView.moreChoice && !this.prizeObj.drawing) {
          zhuge.track('更多机会')
          this.cover(1);
        }
      },

      //弹出遮盖层 
      cover(coverState) {
        this.coverState = coverState;
      },
      //清除已中奖消息
      clearMsg() {
        this.coverState = ''; //页面遮盖
        this.prizeObj.isCover = false; //抽奖时候的奖品遮盖
        this.prizeObj.prizeTemp = null; //
        this.prizeObj.prizeName = ''; //中奖的奖品名字
        this.prizeObj.prizeUrl = ''; //中奖的奖品url
        this.prizeObj.getPrizes = ''; //是否中奖
      },
      //弹出活动说明
      showExplain() {
        if (!this.prizeObj.drawing) {
          this.coverState = 3;
          let explainsDesc = this.pageView.explainsDesc
          if (explainsDesc) {
            this.pageView.explainsDesc = methods.regBr(this.pageView.explainsDesc, this.pageView.explainsDesc)
          } else {
            this.pageView.explainsDesc = '暂无数据'
          }
        }
      },
      showMainPage() {
        this.clearMsg()
      },
    },
    mounted() {
      //   Indicator.open();
      this.getPrizeList();

    },
  }
</script>


