<style lang="less" scoped="">
  .hotPage {
    position: relative;
    height: 100%;
    /*min-height: 45rem;*/
    background-size: 100% 100%;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }
  
  .noShareBtn {
    height: 83%;
  }
  
  .swipe {
    position: relative;
    height: 17rem;
    img {
      width: 100%;
    }
  }
  
  .navbarBox {
    background-color: #fff;
    /*padding: 0 10%;*/
    border-bottom: 1px solid #edeeef;
    .navbarItem {
      display: inline-block;
      width: 49%;
      text-align: center;
    }
    .navbar {
      display: inline-block;
      text-align: center;
      font-size: 1.5rem;
      height: 3rem;
      line-height: 3rem;
      color: #333;
    }
    .navbarChoosed {
      color: #FA005F;
      border-bottom: 3px solid #FA005F;
    }
  }
  
  .contList {
    position: relative;
    padding-top: 1rem;
    .contBox {
      position: relative;
      margin-bottom: 1rem;
      height: 7rem;
      box-sizing: border-box;
      .imgLeft {
        position: relative;
        display: inline-block;
        box-sizing: border-box;
        width: 40%;
        padding: 0 1rem;
        img {
          height: 7rem;
          width: 100%;
        }
      }
      .contRight {
        position: relative;
        display: inline-block;
        box-sizing: border-box;
        height: 7rem;
        vertical-align: top;
        width: 55%;
        .hotTitle {
          color: #333;
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          word-break: break-all;
          font-size: 1.4rem;
        }
        .hotExplain {
          color: #666;
          margin: 3px 0;
          font-size: 1.2rem;
        }
        .hotDate {
          color: #999;
          position: absolute;
          bottom: 0;
          font-size: 1.2rem;
          width: 100%;
          .tips {
            display: inline-block;
          }
        }
      }
    }
  }
</style>

<template>
 
	<div class="hotPage" >
        <div class="swipe" v-if='hotData.adList'>
            <mt-swipe :auto="3000">
                <mt-swipe-item v-for='item in hotData.adList' >
                    <a :href='item.eventOpt? item.eventOpt : ""'>
                        <img :src='item.pic ? item.pic : ""' >
                    </a>
                </mt-swipe-item>
            </mt-swipe>
        </div>
        <div class="navbarBox" >
            <div class="navbarItem" v-for='(item, index) in hotData.columnList'>
                <span class="navbar" :class='{navbarChoosed : column == index}' @click='chooseColumn(index)'>{{item.name}} </span>
            </div>
        </div>
        <div class="contList">
            <div class="contBox" v-for='item in showData'>
                <div class="imgLeft">
                    <img v-lazy="item.thumbnail" />
                </div>
                <div class="contRight">
                  
                    <div class="hotTitle text_ellipsis">{{item.title}}</div>
                    <div class="hotTitle hotExplain text_ellipsis_2">{{item.content}}</div>
                    <div class="hotDate text_ellipsis">{{item.date}} <div class="tips" v-for='x in item.tips'>{{x}}</div></div>
                </div>
            </div>
        </div>
	
	</div>

</template>

<script>
  import Vue from 'vue'
  import axios from 'axios'
  import api from '../api'
  import queryString from 'querystring';
  import hotData from '../../libs/temp/hot_pageView.json';
  import list1 from '../../libs/temp/hot_getColumnMsgList.json';
  import list2 from '../../libs/temp/hot_getColumnMsgList2.json';
  import {
    Indicator,
    Swipe,
    SwipeItem
  } from 'mint-ui';
  Vue.component(Swipe.name, Swipe);
  Vue.component(SwipeItem.name, SwipeItem);

  export default {
    components: {

    },

    data() {
      return {
        hotData: hotData,
        column: '0', //选择的栏目index
        columnList: '',
        showData: [],
        data: list1.contentList, //  内容列表
        data2: list2.contentList, //  内容列表

      }
    },
    methods: {
      chooseColumn(index) { //选择展示的子栏目
        this.showData = [];
        this.column = index
        if (index == 0) {
          this.showData = this.data

        } else if (index == 1) {
          this.showData = this.data2
        }

        // this.column = index; //columnId 是由计算属性computed  计算得出的！！！这里给column 赋值，然后计算！！！

      },

    },
    mounted() {
      this.chooseColumn(0)


    },
  }
</script>


